<template>
  <div class="reg">
    <Titletop title="注册">
      <router-link replace slot="l" :to="{ name: 'login' }"></router-link>
      <router-link slot="r" to="/"></router-link>
    </Titletop>
    <div class="logo"><img src="@/assets/鲸云小说/10002.png" /></div>
    <Forms>
      <van-form @submit="onSubmit">
        <van-field
          type="phone"
          v-model.trim="phone"
          label="+86"
          placeholder="请输入手机号"
        />
        <div style="display: flex">
          <van-field v-model="sms" center clearable placeholder="短信验证码">
          </van-field>
          <van-button
            round
            color="#1989fa"
            type="primary"
            class="sms"
            @click="getsms"
            >{{ countdown }}</van-button
          >
        </div>
        <van-field
          v-model.trim="password"
          type="password"
          name="密码"
          placeholder="6~15位密码,字母或数字组成"
        />
        <div style="margin: 16px 0px">
          <van-button round block type="info" native-type="submit"
            >注册</van-button
          >
        </div>
        <div class="login">
          <router-link to="/login">已有账号登录&gt;</router-link>
        </div>
      </van-form>
    </Forms>
  </div>
</template>

<script>
import Titletop from "@/components/Titletop.vue";
import Forms from "@/components/Forms.vue";
export default {
  components: { Titletop, Forms },
  name: "Myreg",
  data() {
    return {
      password: "",
      phone: "",
      sms: "",
      countdown: "获取验证码",
    };
  },
  methods: {
    getsms() {
      if (this.countdown == "获取验证码") {
        if (!this.phone) {
          this.$toast("请输入手机号");
          return;
        }
        this.axios
          .post("https://jyapi.jyacg.com/web/send_captcha", {
            phone: this.phone,
          })
          .then((res) => {
            if (res.status == 200) {
              if (res.data.code !== 0) {
                this.$toast(res.data.msg);
              } else {
                let a = 60;
                let as = setInterval(() => {
                  this.countdown = a + "s";
                  a--;
                  if (a == 0) {
                    clearInterval(as);
                    this.countdown = "获取验证码";
                  }
                }, 1000);
              }
            }
          });
      }
    },
    onSubmit() {
      if (this.sms && this.countdown !== "获取验证码") {
        console.log(this.password.length);
        if (this.password.length >= 6) {
          this.axios
            .post("https://jyapi.jyacg.com/web/register", {
              phone: this.phone,
              password: this.password,
              captcha: this.sms,
            })
            .then((res) => {
              if (res.status == 200) {
                if (res.data.code == 0) {
                    this.$router.replace({name:'login'})
                    
                }else{
                  this.$toast(res.data.msg);
                }
              }
            });
        } else {
          this.$toast("密码必须是6~15位的数字或字母");
        }
      }
    },
  },
};
</script>

<style scoped lang="scss">
.login{
  display: flex;
  flex-direction: row-reverse;
  a{
    color: #1989fa;
  }
}
.reg {
  height: 667px;
}
.logo {
  text-align: center;
  img {
    margin-top: 15px;
    width: 113px;
    height: 52px;
  }
}
</style>